.App {
  text-align: center;
  padding: 100px 20px;
  display: flex;
  flex-direction: column;
}

body{
  background: rgb(44,40,110);
  background: linear-gradient(90deg, rgba(44,40,110,1) 0%, rgba(49,49,133,1) 39%, rgba(0,176,255,1) 100%);

}   



.container{
  width:fit-content;
  margin: auto;
  margin-bottom: 50px;
  display: flex;

}
.button{
  background-color: #0047AB;
  border: 2px solid aqua;
  border-radius: 50%;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  width: 100px;
  height: 100px;
  transition: 0.7s;
}
.button:hover{
  background-color: #1560DB;
  transition: 0.3s;
}

@media(max-width: 768px){
  .App{
    flex-direction: column-reverse  ;
  }
  .container{
      flex-direction: column;
  }
  .button{
    width: 80px;
    height: 80px;
    padding: 0;
  }

  .gauge{
    width: 200px;
  }
  
}
  


